 @import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap');
 @import url("https://fonts.googleapis.com/css?family=Roboto+Slab:100,300,400,700");
 @import url("https://fonts.googleapis.com/css?family=Raleway:300,300i,400,400,400i,500,500i,600,600i");


 * {
     box-sizing: border-box;
     padding: 0;
     margin: auto;
     position: relative;
     background-color: rgb(29, 32, 39);
 }

 .heading_style {
     padding: 30px 0;
     text-decoration: underline;
     text-align: center;
     font-family: "Raleway", sans-serif;
     text-transform: uppercase;
     letter-spacing: 2px;
     font-weight: 5000;
     color: red;
     background-color: rgba(0, 0, 0, 0.884);
     font-size: 2.5rem;
     margin-bottom: 20px;
 }

 .cards {
     /* 
        margin: 0 auto;
     display: grid;
     grid-template-columns: repeat(auto-fill, minmax(225px 1fr));
     gap: 20px; */
     width: 60%;
     height: auto;



 }

 .card {
     margin: 5%;
     float: left;
     transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
     background-color: rgb(153, 42, 42);
     width: 21.25%;
     border-radius: 12px;
     box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);



 }

 .card:hover {
     box-shadow: 0px 30px 18px -8px rgb(165, 143, 143);
     ;
     transform: scale(1.05, 1.05);
 }

 .card_img {
     width: 100%;
     height: 235px;
     border-top-left-radius: 12px;
     border-top-right-radius: 12px;
 }

 .card_info {
     background-color: rgb(19, 17, 17);
     border-bottom-left-radius: 12px;
     border-bottom-right-radius: 12px;
     padding: 16px 24px 24px 24px;
 }

 .card_category {
     font-family: "Raleway", sans-serif;
     text-transform: uppercase;
     font-size: 13px;
     letter-spacing: 2px;
     font-weight: 500;
     color: #ffffff;
     /* background-color: rgb(0, 0, 0); */
 }

 .card_title {
     margin-top: 5px;
     margin-bottom: 10px;
     font-size: "Raleway", sans-serif;
     color: #fff;
     background-color: rgb(19, 17, 17);
     text-transform: capitalize;
 }

 button {
     padding: 5px 8px;
     font-family: "Raleway", sans-serif;
     text-transform: uppercase;
     font-size: 13px;
     letter-spacing: 2px;
     font-weight: 500;
     color: #ffffff;
     background-color: rgb(86, 3, 3);
     outline: none;
     border: 1px solid rgb(180, 11, 11);
     cursor: pointer;
 }

 @media (max-width: 768px) {
     .card {
         float: none;
         margin: 5%;
         width: 90%;

     }
 }